import xss from "xss";

import {TEXT_STYLES} from "./constants";
import {TextProps} from "./types";

/**    */
export const Text = ({
    text,
    variation,
    color = 'inherit'
}: TextProps) => {
    return (
        <span
            style={{
                color,
                ...TEXT_STYLES[variation]
            }}
            //      
            dangerouslySetInnerHTML={{__html: xss(text)}}
        />
    );
}